<template>
  <div class="content">
    <div class="fontblack title TextTyping">StarConnection</div>
    <div class="wrapper">
      <div class="flex justify-center mt-4">
        <form action="#" class="search-form-001">
          <label>
            <input
              type="text"
              v-model="inputValue"
              placeholder="UID"
              maxlength="9"
              pattern="^[0-9]*$"
            />
            <select v-model="selectedCharacter">
              <option
                v-for="(character, index) in characters"
                :key="index"
                :value="values[index]"
              >
                {{ character }}
              </option>
            </select>
          </label>
          <button @click="validateAndNavigate" class="btn btn--orange"></button>
        </form>
      </div>
      <p class="flex justify-center mt-4 fontblack">Powered by mihomo.me</p>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      inputValue: "", // Holds the entered link
      selectedCharacter: null,
      character1: null,
      character2: null,
      character3: null,
      character4: null,
      character1value: null,
      character2value: null,
      character3value: null,
      character4value: null,
    };
  },
  computed: {
    isValidUid() {
      // UIDが9桁かつ数字であるかをチェック
      return /^\d{9}$/.test(this.inputValue);
    },
    formattedLink() {
      return "/u/" + this.inputValue + "/" + this.selectedCharacter;
    },
    characters() {
      return [
        this.character1,
        this.character2,
        this.character3,
        this.character4,
      ].filter(Boolean);
    },
    values() {
      return [
        this.character1value,
        this.character2value,
        this.character3value,
        this.character4value,
      ].filter(Boolean);
    },
  },
  mounted() {
    this.inputValue = ""; // 初期値を設定
    this.executeApiRequest();
  },
  watch: {
    inputValue(newInputValue) {
      if (newInputValue.length === 9) {
        this.executeApiRequest();
      } else {
        this.responseData = "";
      }
    },
  },
  methods: {
    executeApiRequest() {
      if (this.inputValue !== "") {
        const api =
          window.location.origin +
          "/sr_info_parsed/" +
          this.inputValue +
          "?lang=jp&version=v2";
        axios
          .get(api, {
            validateStatus: function (status) {
              return status >= 200 && status < 300; // 200-299以外のステータスをエラーとする
            },
          })
          .then((response) => {
            if (response.status >= 200 && response.status < 300) {
              const data = response.data;
              this.character1 = data.characters[0]
                ? data.characters[0].name
                : null;
              this.character1value = data.characters[0] ? "0" : null;
              this.character2 = data.characters[1]
                ? data.characters[1].name
                : null;
              this.character2value = data.characters[1] ? "1" : null;
              this.character3 = data.characters[2]
                ? data.characters[2].name
                : null;
              this.character3value = data.characters[2] ? "2" : null;
              this.character4 = data.characters[3]
                ? data.characters[3].name
                : null;
              this.character4value = data.characters[3] ? "3" : null;
            } else {
              // エラーレスポンスが返された場合の処理
              this.responseData = "";
            }
          })
          .catch((error) => {
            // リクエストそのものが失敗した場合の処理
            console.error(error);
            this.responseData = "";
          });
      }
    },
    validateAndNavigate() {
      if (this.isValidUid) {
        this.$router.push(this.formattedLink);
      } else {
        // UIDが9桁かつ数字でない場合の警告処理
        window.alert("UIDは9桁の数字で入力してください。");
      }
    },
  },
};
</script>
<style></style>
